.main-svg {
  display: block;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
}

.action-buttons {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0;

  &__item {
    font-size: 14px;
    margin: 10px 5px;
    cursor: pointer;
    display: inline-block;
    user-select: none;
    padding: 10px;
    border-radius: 7px;
    background-color: rgba(119, 255, 0, 0.5);

    &:hover {
      background-color: rgba(119, 255, 0, 1);
    }
  }
}



